	onLoad() {
		let params = {
			"level": 1
		};
		let url = "/api/user/apprentice";
		this.util.request(url, "GET", params, (res) => {
			if (res.statusCode == 200) {
				if (res.data.code == 1) {
					this.data = res.data.data;
				} else {
					this.util.showWindow(res.data.msg);
				}
			} else {
				this.util.showWindow("请求失败");
			}
		});
	},
	methods: {
		clickCell(index) {
			this.isSelect = index
			this.current = index
			let params = {
				"level": this.isSelect
			};
			let url = "/api/user/apprentice";
			this.util.request(url, "GET", params, (res) => {
				if (res.statusCode == 200) {
					if (res.data.code == 1) {
						this.data = res.data.data;
					} else {
						this.util.showWindow(res.data.msg);
					}
				} else {
					this.util.showWindow("请求失败");
				}
			});

		}
	}
